import React from 'react'
import LocationMap from '../../../components/contact/contact-map'
import styles from './ContactMap.module.scss'

const ContactMap = () => {
    return (
        <div className={`${styles.ammbrAddress} bg--cart-10`}>
            <div className="container">
                <div className="row">
                    <div className="col-12 col-md-7 col-lg-5">
                        <div className={styles.vpContactAddress}>
                            <h2>Out Contact Info</h2>
                            <div className="vp__address__container">
                                {/* Start Single Address */}
                                <div className={styles.vpAddress}>
                                    <h4>Address</h4>
                                    <p>
                                        Hub71 x Wework 16th Floor Rm110 PO Box:
                                        46617 Al Khatem Tower 4 ADGM Square Al
                                        Maryah Island Abu Dhabi, UAE
                                    </p>
                                </div>
                                {/* End Single Address */}
                                {/* Start Single Address */}
                                <div className={styles.vpAddress}>
                                    <h4>Write to us</h4>
                                    <p>
                                        <a href="mailto:info@ammbr.com">
                                            info@ammbr.com
                                        </a>
                                    </p>
                                </div>
                                {/* End Single Address */}
                                {/* Start Single Address */}
                                <div className={styles.vpAddress}>
                                    <h4>Call us </h4>
                                    <p>
                                        <a href="tel:+441312976101">
                                            +44 131 297 6101
                                        </a>
                                    </p>
                                </div>
                                {/* End Single Address */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={styles.googleMap}>
                {/* location map */}
                <LocationMap latitude="47.444" longitude="-122.176" />
            </div>
        </div>
    )
}

export default ContactMap
